.main {
    .container {
        width: 1000px;
        height: 100vh;
        margin: 0 auto;
        display: flex;
        align-items: center;
        visibility: hidden;
    }
}

.img-container {
    width: 1000px;
    height: 500px;
    position: relative;
    overflow: hidden;
    &:after {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        background: white;
    }
    img {
        width: 100%;
        position: absolute;
    }
}